.nav-container nav {
  float: right;
  position: relative;
  z-index: 101; }

.flexnav {
  -webkit-transition: none;
  -moz-transition: none;
  -ms-transition: none;
  transition: none;
  -webkit-transform-style: preserve-3d;
  overflow: hidden;
  margin: 12px auto 0px;
  width: 100%;
  max-height: 0;
  padding: 0px;
  float: right; }
  .flexnav.opacity {
    opacity: 0; }
  .flexnav.flexnav-show {
    max-height: 2000px;
    opacity: 1;
    -webkit-transition: all .5s ease-in-out;
    -moz-transition: all .5s ease-in-out;
    -ms-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out; }
  .flexnav.one-page {
    position: fixed;
    top: 50px;
    right: 5%; }
  .flexnav li {
    font-size: 1.8rem;
    position: relative;
    overflow: hidden;
    padding: 0rem 3.07rem; }
  .flexnav li a {
    position: relative;
    display: block;
    padding: .96em 0em;
    z-index: 2;
    overflow: hidden;
    color: #fff;
    border-bottom: 1px solid rgba(0, 0, 0, 0.15);
    text-transform: uppercase;
    font-weight: bold;
    font-family: "effra", "Arial Black", Gadget, sans-serif;
    text-decoration: none; }
  .flexnav li ul {
    width: 160px;
    width: 16.0rem;
    padding: 0px;
    text-align: center; }
    .flexnav li ul li {
      font-size: 1.6rem;
      position: relative;
      overflow: hidden;
      font-weight: 400; }
  .flexnav li ul.flexnav-show li {
    overflow: visible; }
  .flexnav li ul li a {
    display: block;
    background: #464C52; }
  .flexnav ul li ul li a {
    background: #464C52; }
  .flexnav ul li ul li ul li a {
    background: #464C52; }
  .flexnav .touch-button {
    position: absolute;
    z-index: 999;
    top: 0;
    right: 0;
    width: 50px;
    height: 50px;
    display: none;
    background: #acaca1;
    background: rgba(0, 0, 0, 0.075);
    text-align: center; }
    .flexnav .touch-button:hover {
      cursor: pointer; }
    .flexnav .touch-button .navicon {
      position: relative;
      top: 1.4em;
      font-size: 12px;
      color: #666; }

.menu-button {
  position: relative;
  display: block;
  padding: 1em;
  background: #464C52;
  color: #fff;
  cursor: pointer;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2); }
  .menu-button.one-page {
    position: fixed;
    top: 0;
    right: 5%;
    padding-right: 45px; }
  .menu-button .touch-button {
    background: transparent;
    position: absolute;
    z-index: 999;
    top: 0;
    right: 0;
    width: 50px;
    height: 50px;
    display: none;
    text-align: center;
    display: none; }
    .menu-button .touch-button .navicon {
      font-size: 16px;
